.loginPop{
    
}
.loginPop.hide{
    display: none;
}
.popCover{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #333;
    opacity: .3;
    z-index: 99998;
}
.popMain{
    width: 374px;
    height: 470px;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -180px;
    margin-top: -235px;
    background: #000;
    border: 7px solid #474747;
    border-radius: 15px;
    z-index: 99999;
}
.popHead{
    position: relative;
    height: 160px;
    background: url(../img/moadel_logo.png) no-repeat center;
}
.closePop{
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    font-size: 24px;
    width: 30px;
    line-height: 30px;
    text-align: center;
    height: 30px;
    cursor: pointer;
}
.popBody{
    
}


